html, body {
  font-size: 100%;
  min-height: 100%;
  background-color: #16181c;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.App {
  text-align: center;
  min-height: 90vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: 5vh;
  font-size: calc(10px + 2vmin);
  color: azure;
  background-size: auto 100%;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
  background-color: azure;
  border-radius: 5px;
  margin-bottom: 20px;
}

.App-link {
  color: #61dafb;
}

.button {
  background-color: #282c34;
  border-radius: 5px;
  color: #04AA6D;
  padding: 5px 20px 5px 20px;
  font-family: Lato;
  margin-bottom: 20px;
  width: 30vmin;
}

.button:hover {
  cursor: pointer;
}

.links {
  text-decoration: none;
}
